define('fusor-ember-cli/adapters/application', ['exports', 'ember-data'], function (exports, DS) {

    'use strict';

    var token = $('meta[name="csrf-token"]').attr('content');
    exports['default'] = DS['default'].ActiveModelAdapter.extend({
        namespace: 'api/v21',
        headers: {
            'X-CSRF-Token': token
        }
    });

});